@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  /* -webkit-font-smoothing: antialiased; */
  /* -moz-osx-font-smoothing: grayscale; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

div {
  /* border: 1px solid magenta; */
}

html {
  font-family: 'Press Start 2P', monospace;
}

button, input {
  font-family: 'Press Start 2P', monospace;
}

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-link {
  color: #61dafb;
}

.hidden {
  visibility: hidden;
}

body {
  background-color: rgb(99, 99, 99);
  color: rgb(247, 247, 247);
}

#galleryButton {
  position: absolute;
  top:0;
  left:0;
}
#pixelGridButton {
  position: absolute;
  top:0;
  left: 50px;
}
#webCamButton {
  position: absolute;
  top:0;
  left: 100px;
}
#TestCanvasButton {
  position: absolute;
  top:0;
  left: 150px;
}

#header {
  position: absolute;
  margin: 10px;
  top:0;
  right:0;
}

#info {
  bottom: 0;
  position: absolute;
}

* {
  box-sizing: border-box;
}

.pixelRow {
  /* height: 20px; */
}

.row {
  margin: 0;
  padding: 0;
  line-height: 0;
  /* height: 20px; */
}

.pixel {
  display: inline-block;
  cursor: pointer;
  /* width: 20px;   */
  /* height: 20px;   */
  /* border: 1px solid rgb(98, 98, 98); */
  /* border-width: 0 1px 1px 0; */
}

.pixel:hover {
  /* filter:brightness(10);   */
  box-shadow: inset 0 0 0px 0.5px black;
}

.pixelGrid {
  width: 80vw;
  min-height: 20px;
  margin: 0 auto;
  position: absolute;
  /* top: 50%; */
  /* transform: translateY(-50%); */
  background-color: rgb(50, 50, 50);
}

.pixelWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.eyedropperButton {
  /* position: fixed;
  top: 0;
  right: 0; */
}

.toolBox {
  padding: 3vw;
  position: absolute;
  right: 1%;
  display: flex;
  width: 100px;
  height: 50vh;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
}

#colorPickerButton {
  /* width: 200px; */
  /* height: 100px; */
  color:white
}


.colorPickerButton {
  /* position: fixed;
  bottom: 0;
  right:0; */
}

.sketchWrapper {
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  z-index: 200;
  flex-direction: column;
}

.sketch-picker {
  z-index: 1000;
}

#pickerClose {
  /* position: relative; */
  top: 0;
  right: 0;
  width: 100px;
  background-color: rgb(255, 255, 255);
  z-index: 2000;
  color: black;
  padding: 10px;
  border-radius: 0px 0px 4px 4px;
}

.hidden {
  /* visibility: hidden; */
  display: none;
}

.webcamPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  /* position: absolute;
  margin: 0 auto;
  width: 100vw;
  height: 100vh;
  z-index: 10; */
}

.webcamPopUp {
  background-color: rgb(50, 50, 50);
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 100;
}

.webcamPopUp button {
  z-index: 200;

}

#takePhotoButton {
  /* top:0;
  position: fixed; */
}

.showWebCam {
  box-shadow: 0 0 0 15px black;
  background-color: black;
  margin-bottom: 30px;
  width: 70vw;
}

#webcamLoadingText {
  position: absolute;
}

@media only screen and (max-width: 600px) {
  body {
  }
}

.saveImagePage {
  min-width: 1000px;
  top: 0;
  position: fixed;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 2000;
  background-color: rgb(50, 50, 50);
  /* overflow: scroll; */
}

#saveImageLoadingText {
  position: absolute;
}

.saveImagePage label {
  height: 2rem;
  text-align: center;
  line-height: 2rem;
}

input {
  text-align: center;
  width: 80%;
  height: 2rem;
  border: none;
}

button {
  margin: 5px;
  padding: 10px;
}

#saveInputs {
  width: 30vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.saveImageCanvas {
  box-shadow: 0 0 0 15px black;
  background-color: black;
  margin-bottom: 15px;
}

.chatBox {
  position: absolute;
  left: 1%;
  display: flex;
  width: 15vw;
  height: 50vh;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  /* z-index: -10; */
  background-color: rgb(50, 50, 50);
}

.chatMessages {
  height: 80%;
  width: 90%;
  overflow-x: hidden;
  display: flex;
  flex-direction: column-reverse;
  background-color: rgb(99, 99, 99);
  /* z-index: -10; */
}

#chatInput {
  width: 80%;
}

.drawCanvas:hover {
  cursor:cell;
}

.drawCanvas {
  margin-bottom: 0%;
  margin-top: 0% !important;
  max-width: 1000px;
  max-height: 600px;
  margin: 0 auto;
  box-shadow: 0 0 0 10px #00000075;
}

.gallery {
  height: 100vh;
  width: 80vw;
}

.artwork {
  width: 80%;
  border: 2px solid black;
  margin: 0 auto;
  margin-bottom: 20px;
}

.artwork img {
  width: 100%;
}

.artwork h4 {
  text-align: left;
  margin: 0;
  margin-top: 10px;
  font-size: 1rem;
}

.artworkWrapper {
  width: 80%;
  margin: 0 auto;
  padding: 20px;
}

